.nav-custom {
  background-color: #030007;
  padding: 0.93rem 0;
  border-bottom: 5px solid rgba(6, 4, 8, 0.5);
}

.navlink-custom {
  color: rgb(145, 80, 219);
  font-size: 0.9rem;
}
.navlink-custom:hover {
  color: rgb(213, 185, 245);
  transition: 1s;
}

.bgLogin {
  width: 18rem;
}

.cursor-pointer {
  cursor: pointer;
}

.border-none {
  border: none !important;
}

.border-radius-custom {
  border-radius: 1.3rem;
}

/* user icon display */

.avatar {
  width: auto;
  height: auto;
  border: 2px solid rgb(145, 80, 219);
}

.avatar-default {
  padding: 5px;
  font-size: 35px;
  cursor: pointer;
}

.nav-btn {
  background-color: darkorange;
  border-radius: 1rem;
  margin-right: 0;
  font-size: 12px;
}

.logout-btn {
  font-size: 30px;
  cursor: pointer;
  margin-left: 0;
  color: darkorange;
}

.text-balance {
  margin-right: 0;
}

/* /media queries/ */

@media (min-width: 768px) {
  .nav-btn {
    font-weight: 800;
    background-color: darkorange;
    border-radius: 1rem;
    margin-right: 0;
  }
  .logout-btn {
    font-size: 30px;
    cursor: pointer;
    margin-left: 0;
  }

  .text-balance {
    margin-right: 0;
  }
}

@media (min-width: 992px) {
  .nav-btn {
    font-weight: 800;
    background-color: darkorange;
    border-radius: 1rem;
    margin-right: 1rem;
  }
  .logout-btn {
    font-size: 30px;
    cursor: pointer;
    margin-left: 0.6rem;
  }
  .text-balance {
    margin-right: 0.6rem;
  }
}
