.bg-background {
  background-color: #0a051a;
  border-radius: 1rem;
}

.input-profile {
  background-color: #0a051a !important;
  color: #fff !important;
  border-radius: 1.5rem !important;
}

.profile-input-heading {
  color: #392279 !important;
}

.profile-btn-delete {
  background-color: #ff9c00;
  border: none;
  border-radius: 1.5rem;
  padding: 0.8rem 1.3rem;
  font-size: 1rem;
}

.p-text {
  font-size: 13px;
  font-weight: 500 !important;
}

.bg-white {
  background-color: #fff !important;
}

.bold {
  font-weight: 700 !important;
}
.bolder {
  font-weight: 800;
  font-size: 2rem;
  color: #fff;
}
.rounded {
  border-radius: 1rem !important;
}
.ref-card {
  background-image: url("../../assets/items/ref-img.png");
  background-size: cover;
}

.cardmoney {
  background-image: url("../../assets/items/profile-card.png");
}

.null-msg {
  font-size: 13px;
  color: grey;
}

.profile-btn-openbox {
  background-color: #ff9c00;
  border: none;
  border-radius: 1.5rem;
  padding: 0.8rem 3rem;
  font-size: 0.7rem;
}
.height {
  min-height: 12rem !important;
}
